import React, { Component } from 'react';
import { connect } from 'react-redux';
import Basket from './Basket';
import { incrementProduct } from '../../actions/actions';

class BasketContainer extends Component {
  incrementProductHandler = id => {
    console.log('+1');
    incrementProduct(id);
  };

  render() {
    const { basket } = this.props;
    return <Basket basket={basket} incrementProductHandler={this.incrementProductHandler} />;
  }
}

const mapDispatchToProps = ({ basket }) => {
  return {
    basket
  };
};

export default connect(mapDispatchToProps)(BasketContainer);
